<template>
  <div
    v-if="!$store.state.auth.loggedIn || !$store.state.auth.user.isEmployee"
    class="newsletter-signup"
  >
    <h2 :class="['mb-3 h3', { bold: titleBold }]">{{ title }}</h2>
    <p v-if="intro">{{ intro }}</p>
    <b-form>
      <label class="sr-only sr-only-focusable" for="email2"
        >E-Mail Adresse</label
      >
      <b-form-input
        id="email2"
        v-model="form.email"
        class="mt-2"
        type="email"
        placeholder="E-Mail Adresse eingeben..."
        autocomplete="email"
        required
        trim
        :state="validated ? (form.email ? true : false) : null"
      ></b-form-input>
      <label class="sr-only sr-only-focusable" for="newsletter-state"
        >Bundesland</label
      >
      <b-form-select
        id="newsletter-state"
        v-model="form.state"
        class="mt-2"
        :state="validated ? (form.state ? true : false) : null"
      >
        <b-form-select-option value="" disabled
          >-- Bundesland auswählen --</b-form-select-option
        >
        <b-form-select-option
          v-for="state in companyStateOptions"
          :key="state"
          :value="state"
          >{{ state }}</b-form-select-option
        >
      </b-form-select>
      <b-form-checkbox
        v-model="form.accepted"
        class="mt-2 small"
        name="acceptance"
        :value="true"
        :unchecked-value="false"
        switch
        required
        :state="validated ? (form.accepted ? true : false) : null"
      >
        Ich habe die
        <b-link href="/agbs" target="_blank">
          AGBs
        </b-link>
        und
        <b-link href="/datenschutz" target="_blank">
          Datenschutzbestimmungen
        </b-link>

        gelesen und akzeptiere diese.
      </b-form-checkbox>

      <div class="d-flex justify-content-between mt-3">
        <b-button
          v-if="closeBtn"
          variant="outline-danger"
          @click="$emit('close')"
          >Schließen</b-button
        >
        <b-button
          variant="secondary"
          :disabled="disabled"
          @click.prevent="onSubmit"
        >
          Abonnieren
        </b-button>
      </div>
    </b-form>

    <div v-if="showJobSeekBtn" class="mt-3">
      <b-button
        block
        variant="primary"
        to="/stellengesuche/info"
        @click="$emit('close')"
        >Stellengesuch schalten</b-button
      >
    </div>

    <div v-if="social" class="mt-3">
      <p class="lead mb-0">
        Oder folge uns auf
        <b-link
          :href="`https://www.facebook.com/${$config.fb.path}`"
          class="ml-2"
          target="_blank"
          rel="noopener"
          aria-label="Facebook"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="25"
            height="25"
            fill="currentColor"
            class="bi bi-facebook"
            viewBox="0 0 16 16"
          >
            <path
              d="M16 8.049c0-4.446-3.582-8.05-8-8.05C3.58 0-.002 3.603-.002 8.05c0 4.017 2.926 7.347 6.75 7.951v-5.625h-2.03V8.05H6.75V6.275c0-2.017 1.195-3.131 3.022-3.131.876 0 1.791.157 1.791.157v1.98h-1.009c-.993 0-1.303.621-1.303 1.258v1.51h2.218l-.354 2.326H9.25V16c3.824-.604 6.75-3.934 6.75-7.951z"
            />
          </svg>
        </b-link>
        <b-link
          :href="`https://www.instagram.com/${$config.ig.path}`"
          class="ml-2"
          target="_blank"
          rel="noopener"
          aria-label="Instagram"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="25"
            height="25"
            fill="currentColor"
            class="bi bi-instagram"
            viewBox="0 0 16 16"
          >
            <path
              d="M8 0C5.829 0 5.556.01 4.703.048 3.85.088 3.269.222 2.76.42a3.917 3.917 0 0 0-1.417.923A3.927 3.927 0 0 0 .42 2.76C.222 3.268.087 3.85.048 4.7.01 5.555 0 5.827 0 8.001c0 2.172.01 2.444.048 3.297.04.852.174 1.433.372 1.942.205.526.478.972.923 1.417.444.445.89.719 1.416.923.51.198 1.09.333 1.942.372C5.555 15.99 5.827 16 8 16s2.444-.01 3.298-.048c.851-.04 1.434-.174 1.943-.372a3.916 3.916 0 0 0 1.416-.923c.445-.445.718-.891.923-1.417.197-.509.332-1.09.372-1.942C15.99 10.445 16 10.173 16 8s-.01-2.445-.048-3.299c-.04-.851-.175-1.433-.372-1.941a3.926 3.926 0 0 0-.923-1.417A3.911 3.911 0 0 0 13.24.42c-.51-.198-1.092-.333-1.943-.372C10.443.01 10.172 0 7.998 0h.003zm-.717 1.442h.718c2.136 0 2.389.007 3.232.046.78.035 1.204.166 1.486.275.373.145.64.319.92.599.28.28.453.546.598.92.11.281.24.705.275 1.485.039.843.047 1.096.047 3.231s-.008 2.389-.047 3.232c-.035.78-.166 1.203-.275 1.485a2.47 2.47 0 0 1-.599.919c-.28.28-.546.453-.92.598-.28.11-.704.24-1.485.276-.843.038-1.096.047-3.232.047s-2.39-.009-3.233-.047c-.78-.036-1.203-.166-1.485-.276a2.478 2.478 0 0 1-.92-.598 2.48 2.48 0 0 1-.6-.92c-.109-.281-.24-.705-.275-1.485-.038-.843-.046-1.096-.046-3.233 0-2.136.008-2.388.046-3.231.036-.78.166-1.204.276-1.486.145-.373.319-.64.599-.92.28-.28.546-.453.92-.598.282-.11.705-.24 1.485-.276.738-.034 1.024-.044 2.515-.045v.002zm4.988 1.328a.96.96 0 1 0 0 1.92.96.96 0 0 0 0-1.92zm-4.27 1.122a4.109 4.109 0 1 0 0 8.217 4.109 4.109 0 0 0 0-8.217zm0 1.441a2.667 2.667 0 1 1 0 5.334 2.667 2.667 0 0 1 0-5.334z"
            />
          </svg>
        </b-link>
        <!-- <b-link
          :href="`https://twitter.com/${$config.twitter.path}`"
          class="ml-2"
          target="_blank"
          rel="noopener"
          aria-label="Twitter"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="25"
            height="25"
            fill="currentColor"
            class="bi bi-twitter"
            viewBox="0 0 16 16"
          >
            <path
              d="M5.026 15c6.038 0 9.341-5.003 9.341-9.334 0-.14 0-.282-.006-.422A6.685 6.685 0 0 0 16 3.542a6.658 6.658 0 0 1-1.889.518 3.301 3.301 0 0 0 1.447-1.817 6.533 6.533 0 0 1-2.087.793A3.286 3.286 0 0 0 7.875 6.03a9.325 9.325 0 0 1-6.767-3.429 3.289 3.289 0 0 0 1.018 4.382A3.323 3.323 0 0 1 .64 6.575v.045a3.288 3.288 0 0 0 2.632 3.218 3.203 3.203 0 0 1-.865.115 3.23 3.23 0 0 1-.614-.057 3.283 3.283 0 0 0 3.067 2.277A6.588 6.588 0 0 1 .78 13.58a6.32 6.32 0 0 1-.78-.045A9.344 9.344 0 0 0 5.026 15z"
            />
          </svg>
        </b-link> -->
      </p>
    </div>

    <b-alert
      v-for="error in errors"
      :key="error.message"
      class="mt-3"
      show
      dismissible
      variant="danger"
      >{{ error.message }}</b-alert
    >
  </div>
  <div v-else>
    <h2 class="mb-4 h4">{{ title }}</h2>
    <p v-if="intro">{{ intro }}</p>
    <b-button v-if="$store.state.auth.loggedIn" to="/user/job-newsletter"
      >Jetzt Abonnieren</b-button
    >
  </div>
</template>

<script>
  import { companyStateOptions } from "@/config/formDataConfig.json";
  export default {
    name: "NewsletterSignUpForm",
    props: {
      defaultState: {
        type: String,
        default: null
      },
      title: {
        type: String,
        default: "Deine wöchentlichen Jobangebote"
      },
      titleBold: {
        type: Boolean,
        default: false
      },
      intro: {
        type: String,
        default: ""
      },
      showJobSeekBtn: {
        type: Boolean,
        default: false
      },
      social: {
        type: Boolean,
        default: false
      },
      closeBtn: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        form: {
          email: "",
          state: "",
          accepted: false
        },
        validated: null,
        disabled: false,
        errors: null,
        companyStateOptions
      };
    },
    mounted() {
      if (this.defaultState) {
        this.form.state = this.defaultState;
      }
    },
    methods: {
      async onSubmit() {
        this.errors = false;

        if (!this.formValidation()) {
          this.errors = [
            {
              message: "Bitte fülle die erforderlichen Felder aus!"
            }
          ];
          return null;
        }

        this.$store.dispatch("setOverlay", true);

        try {
          const subscriber = await this.$axios.post("/graphql", {
            query: `
                            mutation {
                                addSubscriber (
                                    email: "${this.form.email}",
                                    state: "${this.form.state}",
                                    accepted: ${this.form.accepted}
                                ) {
                                    _id
                                }
                            }
                        `
          });

          if (subscriber.data.errors) {
            this.errors = subscriber.data.errors;
            throw new Error();
          }

          this.$root.$bvToast.toast(
            "Du wurdest für den Job Newsletter registriert. Bitte überprüfe Dein E-Mail Postfach in den nächsten Minuten (ggfls. auch dein Spam) und bestätige Deine Anmeldung.",
            {
              title: `Newsletter Anmeldung`,
              variant: "success",
              toaster: "b-toaster-bottom-right",
              solid: true,
              noAutoHide: true
            }
          );

          this.disabled = true;
          localStorage.setItem("nl-pop", "false");

          this.$gtag.event("newsletter_subscription", {
            event_label: this.form.state
          });
        } catch (err) {
          // this.error = err.message;
        }

        this.$store.dispatch("setOverlay", false);
      },
      formValidation() {
        this.validated = true;
        return !this.form.email || !this.form.state || !this.form.accepted
          ? false
          : true;
      }
    }
  };
</script>
